.container {
}
.brand {
  // .brand__item
  padding: 201px 0 245px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  @media (max-width: $breakpoint992) {
    flex-wrap: wrap;
    justify-content: center;
    padding: 10px 0 50px 0;
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 455px;
    padding-top: 120px;
    @media (max-width: $breakpoint1200) {
      flex: 0 1 35%;
    }
    @media (max-width: $breakpoint992) {
      flex: 1 0 100%;
      padding-top: 30px;
    }
  }

  // .brand__title

  &__titles {
    font-family: Oswald;
    font-style: normal;
    font-weight: normal;
    font-size: 40px;
    line-height: 50px;
    letter-spacing: 1px;
    text-transform: uppercase;
    @media(max-width: $breakpoint480){
      font-size: 26px;
    }
  }

  // .brand__description

  &__description {
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 15px;
    line-height: 26px;
    margin: 48px 0 37px 0;
    @media (max-width: $breakpoint992) {
      margin: 15px 0 25px 0;
    }
  }

  // .brand__supports

  &__supports {
    display: flex;
    gap: 20px;

  }

  // .brand__customer

  &__customer {
    @media(max-width: $breakpoint480){
      display: flex;
      flex-direction: column;
      flex: 0 1 48%;
    }

  }

  // .brand__photo

  &__photo {
  }

  &__title {
    font-family: Oswald;
    font-style: normal;
    font-weight: normal;
    font-size: 26px;
    line-height: 36px;
    text-transform: uppercase;
    margin: 18px 0 16px 0;
    @media(max-width: $breakpoint480){
      font-size: 20px;
    }
  }

  // .brand__subtext

  &__subtext {
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 24px;
  }

  // .brand__button

  &__button {
    margin-top: 44px;

    a {
      font-family: Oswald;
      font-style: normal;
      font-weight: normal;
      font-size: 13px;
      line-height: 24px;
      text-align: center;
      text-transform: uppercase;
      background-color: $blue;
      padding: 14px 40px;
      border-radius: 30px;
      color: white;
    }
  }

  // .brand__forms

  &__forms {
    box-shadow: 0px 8px 28px 0px #53535326;
    border-radius: 15px;
    max-width: 100%;
    width: 540px;
    position: relative;
    display: flex;
    flex-direction: column;
    @media (max-width: $breakpoint992) {
      flex: 0 1 90%;
      margin-top: 70px;
    }
    @media(max-width: $breakpoint768){
      flex: 1 0 90%;
    }

    .formsright {
      position: absolute;
      top: 77px;
      left: -54px;
    }
    .formsleft {
      position: absolute;
      top: 567px;
      right: -120px;
      z-index: -9999;
    }
    .brand__title {
      margin: 0;
      @media (max-width: $breakpoint992) {
        max-width: 100%;
        width: 100%;
        height: auto;
      }
    }
    .brand__subtext {
      font-family: Oswald;
      font-style: normal;
      font-weight: normal;
      font-size: 29px;
      line-height: 40px;
      text-align: center;
      text-transform: uppercase;
      margin: 57px 0 26px 0;
      @media(max-width: $breakpoint768){
        flex: 1 0 90%;
      }
    }
    .brand__description {
      font-family: Roboto;
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      line-height: 26px;
      text-align: center;
      margin: 0 auto;
      width: 460px;
      margin-bottom: 17px;
      @media(max-width: $breakpoint768){
        width:90%;
      }
    }
  }

  // .brand__label

  &__label {
  }

  // .brand__labels

  &__labels {
    display: flex;
    gap: 25px;
    padding: 0 40px;
    @media(max-width: $breakpoint480){
      padding: 0 10px;
    }


    input {
      font-family: Roboto;
      font-style: normal;
      font-weight: normal;
      font-size: 15px;
      line-height: 60px;
      padding-left: 26px;
      
      width: 218px;
      border: 1px solid #e7e7e7;
      @media(max-width: $breakpoint992){
        width: 100%;
      }
      @media(max-width: $breakpoint768){
        padding-left: 6px;
        width: 100%;
      }
      @media(max-width: $breakpoint480){
        line-height: 42px;
      }
      &:focus {
        outline-color: $blue;
      }
    }
  }
  .inputs {
    display: flex;
    flex-direction: column;
    padding: 0 40px;
    @media(max-width: $breakpoint480){
      padding: 0 10px;
    }
    .inputSubject2 {
    }

    input {
      font-family: Roboto;
      font-style: normal;
      font-weight: normal;
      margin-top: 32px;
      font-size: 15px;
      line-height: 60px;
      padding-left: 26px;
      border: 1px solid #e7e7e7;
      &:focus {
        outline-color: $blue;
      }
      @media(max-width: $breakpoint480){
        line-height: 42px;
      }
    }
  }
  .btn3 {
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 15px;
    line-height: 60px;
    text-align: center;
    background-color: $blue;
    display: block;
    border-radius: 30px;
    color: white;
    margin: 38px 0 55px 0;
  }
}
