.container {
}
footer {
  background: #000000;
  padding: 61px 0 41px 0;
  margin-top: 100px;
}
.footer {
  // .footer__item

  &__item {
  }

  // .footer__links

  &__links {
    display: flex;
    justify-content: space-between;
    align-items: center;
    @media(max-width: $breakpoint768){
        flex-wrap: wrap;
        justify-content: flex-start;
    }
    gap: 20px;
    a {
      font-family: "Oswald";
      font-style: normal;
      font-weight: 400;
      font-size: 12px;
      line-height: 22px;
      letter-spacing: 1px;
      text-transform: uppercase;
      color: white;
      &:hover {
        color: gray;
      }
    }
  }

  // .footer__socials

  &__socials {
  }

  // .footer__powered

  &__powered {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
    margin-top: 63px;
    p {
      font-family: "Oswald";
      font-style: normal;
      font-weight: 400;
      font-size: 12px;
      line-height: 22px;
      text-transform: uppercase;
      color: #999999;
      letter-spacing: 1px;
      span{
        font-family: 'Oswald';
        font-style: normal;
        font-weight: 400;
        font-size: 11px;
        line-height: 22px;
        text-transform: uppercase;
        color: #FFFFFF;
      }
    }
  }

  // .footer__icons

  &__icons {
      i{
          width: 50px;
          height: 50px;
          border-radius: 100%;
          border: 1px solid white;
          color: white;
          text-align: center;
          padding-top:15px;
          cursor: pointer;
          &:hover{
              background-color: white;
              color: black;
          }
      }
  }
}
.fa-brands {
    display: flex;
    align-items: center;
    justify-content: center;
}
