.section {
  // .section__news

  &__news {
  }
}
.container {
}
.news {

  .photonews{
    @media(max-width: $breakpoint480){
      width:150px;
      height: auto;
    }
  }
  // .news__item

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  // .news__tagname

  &__tagname {
    font-family: "Oswald";
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 22px;
    text-align: center;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    color: $blue;
  }

  // .news__title

  &__title {
    font-family: "Oswald";
    font-style: normal;
    font-weight: 400;
    font-size: 40px;
    line-height: 48px;
    text-align: center;
    letter-spacing: 1px;
    text-transform: uppercase;
  }

  // .news__boxs

  &__boxs {
    display: flex;
    align-items: center;
    gap: 16px;
    @media(max-width: $breakpoint1200){
      img{
        width: 230px;
        width: 100%;
        height: auto;
      }
    }
    @media(max-width: $breakpoint992){
      flex-wrap: wrap;
    }
    
  }

  // .news__box1

  &__box1 {
    @media(max-width: $breakpoint1200){
      display: flex;
      flex-direction: column;
    }
    @media(max-width: $breakpoint992){
      flex: 1 0 100%;
    }
  }

  // .news__design

  &__design {
    border: 1px solid #d9dcec;
    display: flex;
    align-items: center;
    gap: 30px;
    margin: 16px 0;
    padding: 21px;
    @media(max-width: $breakpoint1200){
     padding: 10px;
    }
  }

  // .news__design-item

  &__design-item {
  }

  // .news__design-item-title

  &__design-item-title {
    font-family: "Oswald";
    font-style: normal;
    font-weight: 400;
    font-size: 11px;
    line-height: 20px;
    text-transform: uppercase;
    color: $blue;
    @media(max-width: $breakpoint1200){
      width: 100%;
    }
  }

  // .news__design-item-subtext

  &__design-item-subtext {
    font-family: "Oswald";
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 30px;
    text-transform: uppercase;
    margin-bottom: 24px;
    @media(max-width: $breakpoint1200){
      width: 100%;
    }
  }

  // .news__design-item-description

  &__design-item-description {
    font-family: "Roboto";
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    @media(max-width: $breakpoint1200){
      width: 100%;
    }
  }

  // .news__box2

  &__box2 {
    border: 1px solid #d9dcec;
    display: flex;
    align-items: center;
    gap: 30px;
    margin: 16px 0;
    padding: 21px;
    position: relative;
    @media(max-width: $breakpoint1200){
      display: flex;
      flex-direction: column;
      flex: 0 1 48%;
    }
    @media(max-width: $breakpoint992){
      flex: 1 0 100%;
      padding: 10px;
    }

    img {
    }
  }

  // .news__marketing

  &__marketing {
    // .news__marketing_title

    &_title {
      position: absolute;
      left: 51px;
      bottom: 56px;
      z-index: 999;
      font-family: "Oswald";
      font-style: normal;
      font-weight: 400;
      font-size: 11px;
      line-height: 20px;
      text-transform: uppercase;
      color: $blue;
      
      h1 {
        font-family: "Oswald";
        font-style: normal;
        font-weight: 400;
        font-size: 40px;
        line-height: 50px;
        text-transform: uppercase;
        color: black;
        width: 461px;
        @media(max-width: $breakpoint1200){
          width: 100%;
        }
      }
    }
  }
}
