.team {
  padding-top: 120px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: -1;
  }

  // .team__item

  &__item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  // .team__title

  &__title {
    font-family: "Oswald";
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 22px;
    text-align: center;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    color: $blue;
  }

  // .team__subtext

  &__subtext {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 34px;
    text-align: center;
    margin: 23px 0 37px 0;
    width: 776px;
    @media(max-width: $breakpoint768){
        width: 100%;
        font-size: 16px;
    }
  }

  // .team__link

  &__link {
    font-family: 'Oswald';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    text-align: center;
    text-transform: uppercase;
    padding: 14px 40px;
    background-color: $blue;
    color: white;
    border-radius: 30px;
    &:hover{
        box-shadow: 0 0 25px $blue;
        transform: scale(1.05);
    }
  }
}
