.container {
}
.marketing {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 150px;
  padding-bottom: 230px;
  @media (max-width: $breakpoint768) {
    padding-top: 40px;
    padding-bottom: 80px;

  }
  @media (max-width: $breakpoint1200) {
    gap: 20px;
  }
  @media (max-width: $breakpoint992) {
    flex-wrap: wrap;
  }
  // .marketing__item

  &__item {
    @media (max-width: $breakpoint1200) {
      display: flex;
      flex-direction: column;
      flex: 0 1 35%;
    }
    @media (max-width: $breakpoint992) {
      flex: 1 0 100%;
    }
  }

  // .marketing__title

  &__title {
    font-family: Oswald;
    font-style: normal;
    font-weight: normal;
    font-size: 40px;
    line-height: 50px;
    letter-spacing: 1px;
    text-transform: uppercase;
    width: 388px;
    @media (max-width: $breakpoint992) {
      width: 100%;
    }
    @media (max-width: $breakpoint768) {
      font-size: 22px;
    }
  }

  // .marketing__subtext

  &__subtext {
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 24px;
    margin: 46px 0 49px 0;
    width: 540px;
    @media (max-width: $breakpoint992) {
      width: 100%;
    }
    @media(max-width: $breakpoint768){
        margin: 15px 0 20px 0;
    }
  }

  // .marketing__percents

  &__percents {
  }

  // .marketing__percents-one

  &__percents-one {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 30px;
  }

  // .marketing__percents-one--title

  &__percents-one--title {
    font-family: Oswald;
    font-style: normal;
    font-weight: normal;
    font-size: 19px;
    line-height: 30px;
    text-transform: uppercase;
  }

  // .marketing__percents-one-percent

  &__percents-one-percent {
    font-family: Oswald;
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 30px;
    text-align: right;
    text-transform: uppercase;
  }

  // .marketing__percents-one-color

  &__percents-one-color {
    height: 4px;
    width: 100%;
    background: #ebebeb;
    border-radius: 100px;
    .colorBlue {
      height: 4px;
      width: 75%;
      background-color: $blue;
    }
    .colorYellow {
      height: 4px;
      width: 65%;
      background-color: $yellow;
    }
    .colorPurple {
      height: 4px;
      width: 72%;
      background-color: $purple;
    }
  }

  // .marketing__price

  &__price {
    box-shadow: 0px 10px 30px 0px #53535326;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    @media(max-width: $breakpoint480){
        margin-top: 30px;
    }
    @media (max-width: $breakpoint992) {
      flex: 1 0 100%;
    }
  }

  // .marketing__price-title

  &__price-title {
    @media (max-width: $breakpoint1200) {
      max-width: 100%;
      width: 100%;
      height: auto;
    }
  }

  // .marketing__price-subtext

  &__price-subtext {
    font-family: Oswald;
    font-style: normal;
    font-weight: normal;
    font-size: 40px;
    line-height: 50px;
    margin: 31px 0 9px 0;
    text-transform: uppercase;
    @media(max-width: $breakpoint480){
        margin: 15px 0 10px 0;
    }
  }
  &__price-description {
    font-family: Oswald;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 20px;
    text-transform: uppercase;
    margin-bottom: 31px;
    @media(max-width: $breakpoint480){
        margin-bottom: 15px;
    }
  }

  // .marketing__price-lists

  &__price-lists {
    .marketin__price__list {
      font-family: Oswald;
      font-style: normal;
      font-weight: normal;
      font-size: 12px;
      line-height: 22px;
      text-transform: uppercase;
      margin: 18px 0;
    }
  }

  &__price-list {
  }

  // .marketing__price-btn

  &__price-btn {
    font-family: Oswald;
    font-style: normal;
    font-weight: normal;
    font-size: 13px;
    line-height: 24px;
    text-align: center;
    text-transform: uppercase;
    padding: 14px 40px;
    border-radius: 30px;
    background-color: $blue;
    color: white;
    border: 1px solid $blue;
    outline: none;
    margin: 55px 0 40px 0;
    cursor: pointer;
    @media(max-width: $breakpoint480){
        margin-top: 25px;
        width: 90%;
    }
    &:hover {
      color: $blue;
      background-color: white;
    }
  }
}

