.container {
    .more{
        font-family: Oswald;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 26px;
        text-align: center;
        text-transform: uppercase;
        display: flex;
        justify-content: center;
        color: $blue;
      }
}
#photose{
    position: relative;
    margin-bottom: 40px;
  
    .left {
      position: absolute;
      left: 0;
      top: 0;
      z-index: -99;
    }
    .right{
        position: absolute;
        right: 0;
        bottom: 0;
        z-index: -99;
        @media(max-width: $breakpoint768){
            width: 15%;
        }
    }
}
.technology {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;



  // .technology__subtext

  &__subtext {
    font-family: Oswald;
    font-style: normal;
    font-weight: normal;
    font-size: 11px;
    line-height: 22px;
    text-align: center;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    color: $blue;
  }

  // .technology__title

  &__title {
    font-family: Oswald;
    font-style: normal;
    font-weight: normal;
    font-size: 40px;
    line-height: 48px;
    text-align: center;
    letter-spacing: 1px;
    text-transform: uppercase;
    margin: 4px 0 39px 0px;
    @media(max-width: $breakpoint768){
        font-size: 32px;
    }
  }

  // .technology__description

  &__description {
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 26px;
    text-align: center;
    max-width: 693px;
    margin-bottom: 77px;
  }
}
.tecnology {
  // .tecnology__photo

  &__photos {
    .bg1{
        width: 100%;
    height: auto;
    width: 100%;
    }
  }
}
