.section {
  // .section__question

  &__question {
  }
}
.container {
}
.question {
  display: flex;
  justify-content: space-between;
  gap: 120px;
  @media (max-width: $breakpoint992) {
    gap: 20px;
  }
  @media (max-width: $breakpoint992) {
    flex-wrap: wrap;
  }

  // .question__img

  &__img {
    @media (max-width: $breakpoint992) {
      display: flex;
      justify-content: center;
      flex: 1 0 100%;
    }
    img {
      @media (max-width: $breakpoint1200) {
        width: 300px;
      }
      @media (max-width: $breakpoint992) {
        width: 90%;
      }

    }
  }

  // .question__item

  &__item {
    @media (max-width: $breakpoint992) {
        flex: 1 0 100%;
      }
      width: 100%;
  }

  // .question__title

  &__title {
    font-family: Oswald;
    font-style: normal;
    font-weight: normal;
    font-size: 40px;
    line-height: 50px;
    letter-spacing: 1px;
    text-transform: uppercase;
    max-width: 482px;
    @media(max-width: $breakpoint768){
        font-size: 30px;
        width: 100%;
    }
    @media(max-width: $breakpoint480){
        font-size: 24px;
        width: 90%;
    }
  }

  // .question__subtext

  &__subtext {
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 15px;
    line-height: 26px;
    width: 488px;
    margin: 48px 0 58px 0;
    @media(max-width: $breakpoint480){
      max-width: 100%;
      margin: 14px 0 16px 0;
    }
  }

  // .question__box

  &__box {
    display:flex;
    flex-direction: column;
    align-items: flex-start;
  }

  // .question__box-question

  &__box-question {
    padding: 30px 40px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border: 0.5px solid rgba(128, 128, 128, 0.144);
    font-family: Oswald;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 28px;
    width: 100%;
    text-transform: uppercase;
    @media(max-width: $breakpoint768){
        width: 90%;
        font-size: 12px;
        padding: 30px 10px;
    }
    @media(max-width: $breakpoint480){
        width: 100%;
    }
  }
}
