.home {
  // .home__box
  display: flex;
  justify-content: space-between;
  @media (max-width: $breakpoint768) {
    flex-wrap: wrap;
  }

  &__box {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    flex-basis: 484px;
    @media (max-width: $breakpoint768) {
      flex: 1 0 100%;
    }
  }

  // .home__title

  &__title {
    padding-top: 251px;
    font-family: Oswald;
    font-style: normal;
    font-weight: normal;
    font-size: 56px;
    line-height: 66px;
    text-transform: uppercase;
    @media (max-width: $breakpoint992) {
      font-size: 36px;
    }
    @media (max-width: $breakpoint768) {
      padding-top: 120px;
    }

    @media (max-width: $breakpoint480) {
        font-size: 26px;
        line-height: 34px;
    }
  }

  // .home__subtext

  &__subtext {
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 26px;
    @media (max-width: $breakpoint992) {
      font-size: 12px;
    }

    margin: 52px 0 29px 0;
    @media (max-width: $breakpoint768) {
      margin: 10px 0;
    }
  }

  // .home__pricing

  &__pricing {
    // .home__pricing_text
    display: flex;
    align-items: center;
    gap: 20px;
    margin-bottom: 40px;
    @media (max-width: $breakpoint768) {
      margin-bottom: 10px;
    }

    &_text {
      font-family: Oswald;
      font-style: normal;
      font-weight: normal;
      font-size: 18px;
      line-height: 28px;
      text-transform: uppercase;
      @media (max-width: $breakpoint992) {
        font-size: 14px;
      }
    }
  }

  // .home__btn

  &__btn {
    font-family: Oswald;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 24px;
    text-align: center;
    text-transform: uppercase;
    padding: 14px 40px;
    background: $yellow;
    border-radius: 30px;
    cursor: pointer;
    color: white;
    @media(max-width: $breakpoint480){
      width: 100%;
    }
    &:hover {
      background-color: #fc9921;
    }
    @media (max-width: $breakpoint480) {
        padding: 10px 16px;
        font-size: 10px;
    }
  }

  // .home__box2

  &__box2 {
    padding-top: 132px;
    @media (max-width: $breakpoint992) {
      display: flex;
      flex-direction: column;
      justify-content: center;
      flex-basis: 400px;
    }
    @media (max-width: $breakpoint768) {
      flex: 1 0 100%;
      padding-top: 30px;
      align-items: center;
    }
    img {
      max-width: 700px;
      @media (max-width: $breakpoint1200) {
        max-width: 600px;
      }
      @media (max-width: $breakpoint768) {
        max-width: 100%;
        width: 100%;
      }
    }
  }
}
