nav {
  background-color: white;
  transition: all 0.4s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 9999;
  .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 10vh;
  }

  .logo {
    display: block;
    text-decoration: none;
    font-size: 3em;
    color: black;
    font-weight: bold;
    transition: all 0.4s;
    margin-right: 60px;
  }

  ul {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 40px;
    margin: 0;
    li {
      a {
        font-family: Oswald;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 26px;
        text-transform: uppercase;
        color: $black;
        &:hover{
          color: $blue;
        }
      }
    }
  }
  .btn {
    font-family: Oswald;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 24px;
    text-align: center;
    text-transform: uppercase;
    color: white;
    background-color: $blue;
    padding: 14px 40px;
    border-radius: 30px;
    @media(max-width: $breakpoint768){
      padding: 10px 16px;
      font-size: 10px;
    }
    &:hover{
      background-color: #003fbd;
    }
  }
  .burger {
    display: none;
    cursor: pointer;
    div {
      width: 30px;
      height: 3px;
      background-color: black;
      margin: 7px 0;
      transition: all 0.4s ease-in-out;
    }
  }
  .logoMedia{
    display: none;
  }
}

.nav-active {
  transform: translateX(0%);
}

.toggle .line1 {
  width: 30px;
  transform: rotate(45deg) translateY(14px);
  background-color: black;
}
.toggle .line2 {
  transform: translateX(100%);
  opacity: 0;
}
.toggle .line3 {
  width: 30px;
  transform: rotate(315deg) translateY(-14px);
}

@media (max-width: 850px) {
  nav {
    justify-content: space-between;
    padding: 0 0.5rem;
    ul {
      position: absolute;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      background-color: rgba(245, 245, 245, 0.164);
      backdrop-filter: blur(8.3px);
      -webkit-backdrop-filter: blur(8.3px);
      top: 10vh;
      right: 0px;
      width: 50%;
      height: 90vh;
      transform: translateX(100%);
      transition: all 0.5s ease-in-out;
    }
    .burger {
      display: block;
    }
    .logoMedia{
      display: block;
    }
    .logo{
      display: none;
    }
  }

}
