.copmany {


    // .copmany__item

    &__item {
        display: flex;
        align-items: center;
        gap: 60px;
        margin-top: 78px;
        margin-bottom: 98px;
        padding: 65px 0px;
        border-top: 1px solid #E7E7E7;
        border-bottom: 1px solid #E7E7E7;
        @media(max-width: $breakpoint1200){
            flex-wrap: wrap;
        }
        @media(max-width: $breakpoint768){
            justify-content: center;
        }
        @media(max-width: $breakpoint480){
            flex: 1 0 100%;
            gap: 20px;
        }
    }
}
