.section {
  // .section__feedback

  &__feedback {
    margin-bottom: 200px;
    @media(max-width: $breakpoint768){
      margin-bottom: 30px;
    }
  }
}
.feedback {
  // .feedback__title

  &__title {
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 24px;
    text-align: center;
  }

  // .feedback__photo

  &__photo {
    margin: 6px 0 20px 0;
  }

  // .feedback__name

  &__name {
    font-family: Oswald;
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 30px;
    text-align: center;
    text-transform: uppercase;
  }

  // .feedback__job

  &__job {
    font-family: Oswald;
    font-style: normal;
    font-weight: normal;
    font-size: 13px;
    line-height: 24px;
    text-align: center;
    text-transform: uppercase;
    margin-bottom: 10px;
  }

  // .feedback__stars

  &__stars {
    display: flex;
    align-items: center;
  }
}

.swiper {
  width: 100%;
  height: 100%;
  overflow: visible;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  flex-direction: column;
  align-items: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
  border: 1px solid #e7e7e7;
  box-sizing: border-box;
  border-radius: 10px;
  padding: 45px 47px;
  cursor: pointer;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

