* {
  font-family: "Oswald", sans-serif;
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  box-sizing: border-box;
}

html, body {
  overflow-x: hidden;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

nav {
  background-color: white;
  transition: all 0.4s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 9999;
}
nav .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 10vh;
}
nav .logo {
  display: block;
  text-decoration: none;
  font-size: 3em;
  color: black;
  font-weight: bold;
  transition: all 0.4s;
  margin-right: 60px;
}
nav ul {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 40px;
  margin: 0;
}
nav ul li a {
  font-family: Oswald;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 26px;
  text-transform: uppercase;
  color: #000000;
}
nav ul li a:hover {
  color: #0057FF;
}
nav .btn {
  font-family: Oswald;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;
  text-align: center;
  text-transform: uppercase;
  color: white;
  background-color: #0057FF;
  padding: 14px 40px;
  border-radius: 30px;
}
@media (max-width: 767.98px) {
  nav .btn {
    padding: 10px 16px;
    font-size: 10px;
  }
}
nav .btn:hover {
  background-color: #003fbd;
}
nav .burger {
  display: none;
  cursor: pointer;
}
nav .burger div {
  width: 30px;
  height: 3px;
  background-color: black;
  margin: 7px 0;
  transition: all 0.4s ease-in-out;
}
nav .logoMedia {
  display: none;
}

.nav-active {
  transform: translateX(0%);
}

.toggle .line1 {
  width: 30px;
  transform: rotate(45deg) translateY(14px);
  background-color: black;
}

.toggle .line2 {
  transform: translateX(100%);
  opacity: 0;
}

.toggle .line3 {
  width: 30px;
  transform: rotate(315deg) translateY(-14px);
}

@media (max-width: 850px) {
  nav {
    justify-content: space-between;
    padding: 0 0.5rem;
  }
  nav ul {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: rgba(245, 245, 245, 0.164);
    backdrop-filter: blur(8.3px);
    -webkit-backdrop-filter: blur(8.3px);
    top: 10vh;
    right: 0px;
    width: 50%;
    height: 90vh;
    transform: translateX(100%);
    transition: all 0.5s ease-in-out;
  }
  nav .burger {
    display: block;
  }
  nav .logoMedia {
    display: block;
  }
  nav .logo {
    display: none;
  }
}
